import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { NotfoundComponent } from './layout/notfound/notfound.component';
import { AppLayoutComponent } from './layout/app.layout.component';
import { AuthGuard } from './guards/auth.guard';
import { OrdenesTrabajoComponent } from './layout/ordenes-trabajo/ordenes-trabajo.component';
import { MostrarTareaComponent } from './layout/mostrar-tarea/mostrar-tarea.component';
import { GastosComponent } from './layout/gastos/gastos.component';
import { PdvComponent } from './layout/pdv/pdv.component';
import { MostrarOrdenTrabajoComponent } from './layout/ordenes-trabajo/mostrar-orden-trabajo/mostrar-orden-trabajo.component';
import { AlmacenComponent } from './layout/almacen/almacen.component';
import { IngresarStockComponent } from './layout/almacen/ingresar-stock/ingresar-stock.component';
import { CrearProductoComponent } from './layout/almacen/crear-producto/crear-producto.component';
import { CrearProductoPropioComponent } from './layout/almacen/crear-producto-propio/crear-producto-propio.component';
import { EscanerComponent } from './layout/almacen/escaner/escaner.component';
import { FacturaReaderComponent } from './layout/factura-reader/factura-reader.component';
import { ConfirmarOrdenCompraComponent } from './layout/confirmar-orden-compra/confirmar-orden-compra.component';

@NgModule({
  imports: [
    RouterModule.forRoot(
      [
        {
          path: '',
          component: AppLayoutComponent,
          canActivate: [AuthGuard],
          children: [
            {
              path: '',
              loadChildren: () =>
                import('./layout/dashboard/dashboard.module').then(
                  (m) => m.DashboardModule
                ),
            },
            {
              path: 'ordenes-trabajo',
              component: OrdenesTrabajoComponent,
            },
            {
              path: 'ordenes-trabajo/mostrar/:id',
              component: MostrarOrdenTrabajoComponent,
            },
            {
              path: 'tarea/:id',
              component: MostrarTareaComponent,
            },
            {
              path: 'gastos',
              component: GastosComponent,
            },
            {
              path: 'pdv',
              component: PdvComponent,
            },
            {
              path: 'almacen',
              component: AlmacenComponent,
            },
            {
              path: 'almacen/ingresar-stock',
              component: IngresarStockComponent,
            },
            {
              path: 'almacen/scanner',
              component: EscanerComponent,
            },
            {
              path: 'almacen/nuevo-producto/:barcode',
              component: CrearProductoComponent,
            },
            {
              path: 'almacen/nuevo-producto-propio',
              component: CrearProductoPropioComponent,
            },
            {
              path: 'almacen/confirmar-orden-compra',
              component: ConfirmarOrdenCompraComponent,
            },
            {
              path: 'factura-reader/:id',
              component: FacturaReaderComponent
            },
          ],
        },
        {
          path: 'auth',
          loadChildren: () =>
            import('./auth/auth.module').then((m) => m.AuthModule),
        },
        { path: 'notfound', component: NotfoundComponent },
        { path: '**', redirectTo: '/notfound' },
      ],
      {
        scrollPositionRestoration: 'enabled',
        anchorScrolling: 'enabled',
        onSameUrlNavigation: 'reload',
      }
    ),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
