import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { Usuario } from 'src/app/interfaces/login/login-respuesta.interface';
import { OrdenTrabajo } from 'src/app/interfaces/tarea.interface';
import { UsuarioService } from 'src/app/services/auth/usuario.service';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-ordenes-trabajo',
  templateUrl: './ordenes-trabajo.component.html',
  styleUrls: ['./ordenes-trabajo.component.scss'],
})
export class OrdenesTrabajoComponent implements OnInit, OnDestroy {
  private readonly tareaService = inject(TareaService);
  private readonly messageService = inject(CustomMessageService);
  private readonly pruebasService = inject(PruebasService);

  usuario = JSON.parse(localStorage.getItem('usuario'));

  ordenesTrabajo: OrdenTrabajo[];

  ordenesTrabajoToShow = [];
  ordenesTrabajoProcesoToShow = [];
  ordenesTrabajoFinalizadoToShow = [];

  otrasSubscripciones = [];

  ngOnInit(): void {
    // this.ordenesTrabajo = this.pruebasService.getOrdenesTrabajos();
    // this.ordenesTrabajo.forEach((orden) => {
    //   let lineas = [];
    //   orden.lineasTareas.forEach((lineaTarea) => {
    //     lineas.push({
    //       id: lineaTarea.id,
    //       label: lineaTarea.accion,
    //       icon: 'pi pi-fw pi-plus',
    //       routerLink: `/tarea/${lineaTarea.id}`,
    //     });
    //   });
    //   this.ordenesTrabajoToShow.push({
    //     label: orden.nombre,
    //     items: lineas,
    //   });
    // });

    this.otrasSubscripciones.push(
      this.tareaService.getOrdenesTrabajosDe(this.usuario.id).subscribe(
        (response) => {
          console.log(response);
          this.ordenesTrabajo = response;
          this.ordenesTrabajo.forEach((orden) => {
            this.ordenesTrabajoToShow.push({
              label: orden.nombre,
              routerLink: `/ordenes-trabajo/mostrar/${orden.id}`,
            });
          });
          console.log(this.ordenesTrabajoToShow);
        },
        (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      ),
    );
  }

  ngOnDestroy() {
    this.otrasSubscripciones.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
