import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import {
  Mercancia,
  MercanciaPaginadas,
  MercanciaPorUbi,
  MercanciaStock,
  mercanciaBarcodeResponse,
} from '../interfaces/mercancia.interface';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class MercanciaService {
  constructor(private http: HttpClient) {}

  private API_URL = environment.API_URL;

  getMercanciaFromBarcode(
    codigoEan: string,
  ): Observable<mercanciaBarcodeResponse> {
    const url = this.API_URL + `/producto/verificar_codigo_ean/${codigoEan}`;
    return this.http.get<mercanciaBarcodeResponse>(url);
  }

  createMercancia(newMercancia: Mercancia): Observable<Mercancia> {
    const url = this.API_URL + `/mercancia`;
    return this.http.post<Mercancia>(url, newMercancia);
  }

  getMercancias(): Observable<Mercancia[]> {
    const url = this.API_URL + `/mercancias`;
    return this.http.get<Mercancia[]>(url);
  }

  getMercanciasPaginadas(url: string): Observable<MercanciaPaginadas> {
    return this.http.get<MercanciaPaginadas>(url);
  }

  getMercanciasByProducto(idProducto): Observable<Mercancia[]> {
    const url = this.API_URL + `/mercancias/producto/${idProducto}`;
    return this.http.get<Mercancia[]>(url);
  }

  // getMercanciasByUbi(idMerc): Observable<Mercancia[]> {
  //   const url = this.API_URL + `/mercancias/ubicacion/${idMerc}`;
  //   return this.http.get<Mercancia[]>(url);
  // }

  actualizarStock(body: any): Observable<MercanciaStock> {
    const url = this.API_URL + `/producto/cambiar_stock`;
    return this.http.post<MercanciaStock>(url, body);
  }

  cambiarStockEnUbi(body: any): Observable<MercanciaStock> {
    const url = this.API_URL + `/mercancia_ubicacion`;
    return this.http.put<MercanciaStock>(url, body);
  }

  cambiarPrecioMerca(body: any): Observable<MercanciaStock> {
    const url = this.API_URL + `/mercancia`;
    return this.http.put<MercanciaStock>(url, body);
  }

  getMercanciasByUbicacion(idUbi: number): Observable<MercanciaPorUbi[]> {
    const url = this.API_URL + `/mercancias/ubicacion/${idUbi}`;
    return this.http.get<MercanciaPorUbi[]>(url);
  }
}
