import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { FileSelectEvent } from 'primeng/fileupload';
import { Subscription } from 'rxjs';
import { Presupuesto } from 'src/app/interfaces/presupuesto.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { PresupuestoService } from 'src/app/services/presupuesto.service';
import { PruebasService } from 'src/app/services/pruebas.service';

@Component({
  selector: 'app-gastos',
  templateUrl: './gastos.component.html',
  styleUrls: ['./gastos.component.scss'],
})
export class GastosComponent implements OnInit, OnDestroy {
  // servicios requeridos
  private readonly pruebaService = inject(PruebasService);
  private readonly presupuestoService = inject(PresupuestoService);
  private readonly activatedRoutes = inject(ActivatedRoute);
  private readonly router = inject(Router);
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);

  public compuestoSnOptions: Object[] = [
    { value: 'Si', label: 'Si' },
    { value: 'No', label: 'No' },
  ];

  public hadSubmit: boolean = false;

  public loading = false;

  public loadingTipos = true;

  public presupuestos: Presupuesto[] = [];

  public imagenSubida: any;

  public subs: Subscription[] = [];

  public newPDV = this.Form.group({
    presupuesto: [null, Validators.required],
    imagen: [null, Validators.required],
  });

  // form getters ----------------------------------------------------------------------------------

  get presupuesto() {
    return this.newPDV.get('presupuesto') as UntypedFormControl;
  }
  get imagen() {
    return this.newPDV.get('imagen') as UntypedFormControl;
  }

  // end form getter --------------------------------------------------------------------------------

  ngOnInit(): void {
    this.cargarPresupuestos();
  }

  validar(campo: string) {
    return this.newPDV.get(campo).valid;
  }

  submit() {
    this.hadSubmit = true;

    this.loading = true;

    if (this.newPDV.invalid) {
      console.log(this.newPDV.invalid);

      this.loading = false;
      this.messageService.error('Verifique los campos')
      return;
    }

    const gastoToSend = {
      imagen: this.imagen.value,
      id: this.presupuesto.value,
    };

    this.cargarImagenPresupuesto(gastoToSend);
  }

  // dinamica de la imagen -----------------------------------------------------------------------

  onSelectImg(event: FileSelectEvent) {
    let fileReader = new FileReader();

    fileReader.readAsDataURL(event['currentFiles'][0]);

    fileReader.onload = () => {
      this.imagen.setValue(fileReader.result);
      this.imagenSubida = fileReader.result;
    };
  }

  onRemoveImg() {
    this.imagen.setValue(null);
    this.imagenSubida = null;
  }

  // llamadas al back -----------------------------------------------------------------------------

  // cargo los tipos de productos del select homonimo
  cargarImagenPreviaDelPresupuesto(id) {
    console.log(id);

    // this.presupuestos = this.pruebaService.getPresupuestos()
    this.subs.push(
      this.presupuestoService.getPresupuesto(id).subscribe(
        (response) => {
          this.imagenSubida = response.imagen;
        },
        (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      ),
    );
  }
  // cargo los presupeustos del select homonimo
  cargarPresupuestos() {
    // this.presupuestos = this.pruebaService.getPresupuestos()
    this.subs.push(
      this.presupuestoService.getPresupuestos().subscribe(
        (response) => {
          this.presupuestos = response;
          console.log(this.presupuestos);
        },
        (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      ),
    );
  }

  cargarImagenPresupuesto(gastoToSend) {
    console.log(gastoToSend);
    this.subs.push(
      this.presupuestoService
        .editPresupuesto(gastoToSend as Presupuesto)
        .subscribe(
          (response) => {
            console.log(response);
            this.messageService.add({
              severity: 'success',
              summary: 'Exito!',
              detail: 'PDV registrado con exito!',
            });
            console.log(response);
            this.router.navigateByUrl('/');
          },
          (error) => {
            console.log(error);

            this.loading = false;
            this.messageService.error(error.error.detail)
            return;
          },
        ),
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
