import { Component, OnInit, OnDestroy, inject, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { FileSelectEvent } from 'primeng/fileupload';
import { ImagenLineaTarea, LineaTarea } from 'src/app/interfaces/tarea.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-tarea-documentacion',
  templateUrl: './mostrar-tarea-documentacion.component.html',
  styleUrls: ['./mostrar-tarea-documentacion.component.scss'],
})
export class MostrarTareaDocumentacionComponent implements OnInit, OnDestroy {
  private readonly tareaService = inject(TareaService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly messageService = inject(CustomMessageService);
  // private readonly pruebasService = inject(PruebasService);

  cardMenu: MenuItem[];

  @Input() tarea: LineaTarea;

  subcripciones = [];

  idTarea: number = -1;

  estadoTareaIniciada: boolean = null;

  imagenSubida: string;

  imagenes: ImagenLineaTarea[];

  ngOnInit(): void {
    this.subcripciones.push(
      this.tareaService.getImagenesLineaTarea(this.tarea.id).subscribe({
        next: (response) => {
          // console.log(response);
          this.imagenes = response;
        },
        error: (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      }),
    );
  }

  // dinamica de la imagen -----------------------------------------------------------------------

  onSelectImg(event: FileSelectEvent) {
    let fileReader = new FileReader();

    fileReader.readAsDataURL(event['currentFiles'][0]);

    fileReader.onload = () => {
      this.imagenSubida = fileReader.result as string;
    };
  }

  onRemoveImg() {
    this.imagenSubida = null;
  }

  subirImagen() {
    this.subcripciones.push(
      this.tareaService
        .subirImagenLineaTarea({ id: this.tarea.id, imagen: this.imagenSubida })
        .subscribe({
          next: (response) => {
            this.messageService.add({
              severity: 'success',
              summary: 'Exito',
              detail: 'la imagen a sido subida',
            });
            this.onRemoveImg();
            this.ngOnInit();
          },
          error: (error) => {
            console.log(error);
            this.messageService.error(error.error.detail)
          },
        }),
    );
  }

  borrarImagen(id: number) {
    this.subcripciones.push(
      this.tareaService.borrarImagenLineaTarea(id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la imagen a sido borrada',
          });
          this.ngOnInit();
        },
        error: (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      }),
    );
  }

  ngOnDestroy(): void {
    this.subcripciones.forEach((subcripcion) => {
      subcripcion.unsubscribe();
    });
  }
}