import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Almacen, AlmacenesPaginados } from '../interfaces/almacenes.interface';

@Injectable({
  providedIn: 'root',
})
export class AlmacenesService {
  private readonly http = inject(HttpClient);

  private API_URL = environment.API_URL;

  getAlmacenes(): Observable<Almacen[]> {
    const url = this.API_URL + `/almacenes`;
    return this.http.get<Almacen[]>(url);
  }

  getAlmacenesPaginados(url): Observable<AlmacenesPaginados> {
    return this.http.get<AlmacenesPaginados>(url);
  }

  getAlmacen(id: number): Observable<Almacen> {
    const url = this.API_URL + `/almacen/${id}`;
    return this.http.get<Almacen>(url);
  }

  createAlmacen(newPresupeusto: Almacen): Observable<Almacen> {
    const url = this.API_URL + `/almacen`;
    return this.http.post<Almacen>(url, newPresupeusto);
  }

  editAlmacen(newPresupeusto: Almacen): Observable<Almacen> {
    const url = this.API_URL + `/almacen`;
    return this.http.put<Almacen>(url, newPresupeusto);
  }

  deleteAlmacen(id: number): Observable<Almacen> {
    const url = this.API_URL + `/almacen/${id}`;
    return this.http.delete<Almacen>(url);
  }
}
