import { Component, OnInit, inject } from '@angular/core';
import { Router } from '@angular/router';
import { MercanciaService } from 'src/app/services/mercancia.service';
import { CustomMessageService } from 'src/app/services/custom-message.service';

@Component({
  selector: 'app-escaner',
  templateUrl: './escaner.component.html',
  styleUrls: ['./escaner.component.scss'],
})
export class EscanerComponent implements OnInit {
  private readonly mercanciaService = inject(MercanciaService);
  private readonly messageService = inject(CustomMessageService);
  private readonly router = inject(Router);

  public scannerActivo: boolean = false;

  public codigoEan: string = '';

  public hadSubmit: boolean = false;

  public loading: boolean = false;

  public timeout = null;

  public codeInputLength: number = 0;

  public soloNumeros: boolean;

  // allowedFormats = [
  //   BarcodeFormat.EAN_13,
  //   BarcodeFormat.EAN_8,
  //   BarcodeFormat.CODE_128,
  //   BarcodeFormat.UPC_A,
  //   BarcodeFormat.UPC_E,
  //   BarcodeFormat.UPC_EAN_EXTENSION,
  // ];

  ngOnInit(): void {
    window.scrollTo(0, 0);
  }

  buscar() {
    console.log(this.codigoEan);
    this.getCodigo(this.codigoEan);
  }

  // navigateToAnadirMercancia(codigoEan, idMercancia, cantidad = 0) {
  //   this.router.navigateByUrl(
  //     `/anadir-mercancia/${codigoEan}/${idMercancia}/${cantidad}`
  //   );
  // }

  espera(event) {
    this.codeInputLength = event.target.value.length;

    const regex = /^[0-9]*$/;
    this.soloNumeros = regex.test(event.target.value);

    if (event.target.value.length === 13 && this.soloNumeros) {
      this.buscar();
    }
  }

  navigateToNuevoProducto(codigoEan) {
    this.router.navigateByUrl(`almacen/nuevo-producto/${codigoEan}`);
  }

  getCodigo(codigoEan) {
    this.hadSubmit = true;

    if (codigoEan === '') {
      return;
    }
    // 1234567891011 codigo correcto para purebas
    // barcode = 1234567891011234; //codigo incorrecto para pruebas
    this.loading = true;
    // console.log(codigoEan);
    // this.messageService.add({
    //   severity: 'success',
    //   summary: 'Codigo Obtenido',
    //   detail: '',
    // });
    this.mercanciaService.getMercanciaFromBarcode(codigoEan).subscribe(
      (response) => {
        console.log(response);
        // this.disableScanner();
        this.loading = false;
        this.messageService.alert(
          'Ese codigo ya esta registrado, intente con otro',
        );
        // this.navigateToAnadirMercancia(codigoEan, response.id, response.stock);
      },
      (error) => {
        if (
          error.status == 404 &&
          error.error.detail == 'Codigo de Barras no encontrado'
        ) {
          // this.disableScanner();
          this.navigateToNuevoProducto(codigoEan);
          this.loading = false;
        } else {
          console.error(error);
          this.loading = false;
          this.messageService.error('Error al verificar el codigo producto');
          return;
        }
      },
    );
  }

  // enableScanner() {
  //   this.scannerActivo = true;
  //   this.disableNotFound();
  // }

  // disableScanner() {
  //   this.scannerActivo = false;
  //   $('#exampleModalCenter').modal('hide');
  // }
}
