import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { LineaTarea, LineaTareaPost } from 'src/app/interfaces/tarea.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-tarea',
  templateUrl: './mostrar-tarea.component.html',
  styleUrls: ['./mostrar-tarea.component.scss'],
})
export class MostrarTareaComponent implements OnInit, OnDestroy {
  private readonly tareaService = inject(TareaService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly router = inject(Router);
  private readonly messageService = inject(CustomMessageService);
  // private readonly pruebasService = inject(PruebasService);

  cardMenu: MenuItem[];

  tarea: LineaTarea;

  subcripciones = [];

  idTarea: number = -1;

  estadoTareaIniciada: boolean = null;

  tipoTarea: string;

  public tipoTareaOptions: Object[] = [
    { value: 'tiempo', label: 'tiempo' },
    { value: 'documentacion', label: 'documentacion' },
    { value: 'finalizacion', label: 'finalizacion' },
  ];

  ngOnInit(): void {
    this.subcripciones.push(
      this.activatedRoute.params.subscribe((params) => {
        this.idTarea = params['id'];
      }),
    );
    this.subcripciones.push(
      this.tareaService.getLineaTarea(this.idTarea).subscribe(
        (response) => {
          console.log(response);
          this.tarea = response;
          this.tipoTarea = response.tipo;
          this.tareaService.getEstadoTimeTarea(this.idTarea).subscribe({
            next: (response) => {
              console.log(response);
              this.estadoTareaIniciada = response.tareaIniciada;
            },
            error: (error) => {
              console.log(error);
              this.messageService.error(error.error.detail)
            },
          });
        },
        (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      ),
    );
  }

  back() {
    let lastUrl = sessionStorage.getItem('lastUrl');
    this.router.navigateByUrl(lastUrl);
  }

  cambioEstadoTareaInciada(event) {
    this.ngOnInit();
  }

  cambiarTipoTarea() {
    this.tareaService
      .editarLineaTarea({
        id: this.tarea.id,
        tipo: this.tipoTarea,
      } as LineaTarea)
      .subscribe(
        (response) => {
          console.log(response);
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'El tipo de la tarea ha sido actualizado',
          });
          this.ngOnInit();
        },
        (error) => {
          console.log(error);
          this.messageService.error(error.error.detail)
        },
      );
  }

  ngOnDestroy(): void {
    this.subcripciones.forEach((subcripcion) => {
      subcripcion.unsubscribe();
    });
  }
}
