import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { LineaOrdenCompra, LineaOrdenCompraPost, OrdenCompra, OrdenCompraPost, OrdenesComprasPaginadas } from '../interfaces/orden-compra.interface';

@Injectable({
  providedIn: 'root'
})
export class OrdenesService {
  private readonly http = inject(HttpClient);

  private API_URL = environment.API_URL;

  getOrdenesCompras(): Observable<OrdenCompra[]> {
    const url = this.API_URL + `/ordenes_compras`;
    return this.http.get<OrdenCompra[]>(url);
  }

  getOrdenesComprasPaginadas(url): Observable<OrdenesComprasPaginadas> {
    return this.http.get<OrdenesComprasPaginadas>(url);
  }

  getOrdenCompra(id: number): Observable<OrdenCompra> {
    const url = this.API_URL + `/orden_compra/${id}`;
    return this.http.get<OrdenCompra>(url);
  }

  createOrdenCompra(newPresupeusto: OrdenCompraPost): Observable<OrdenCompra> {
    const url = this.API_URL + `/orden_compra`;
    return this.http.post<OrdenCompra>(url, newPresupeusto);
  }

  editOrdenCompra(newPresupeusto: OrdenCompra): Observable<OrdenCompra> {
    const url = this.API_URL + `/orden_compra`;
    return this.http.put<OrdenCompra>(url, newPresupeusto);
  }

  deleteOrdenCompra(id: number): Observable<OrdenCompra> {
    const url = this.API_URL + `/orden_compra/${id}`;
    return this.http.delete<OrdenCompra>(url);
  }

  //-------------------------------------------------------------------------------------------

  getLineasOrdenesComprasDisponibles(): Observable<LineaOrdenCompra[]> {
    const url = this.API_URL + `/ordenes_compras_lineas_disponibles`
    return this.http.get<LineaOrdenCompra[]>(url);
  }

  getLineasOrdenesComprasByOrdenCompra(id: number): Observable<LineaOrdenCompra[]> {
    const url = this.API_URL + `/ordenes_compras/ordenes_compras_lineas/${id}`
    return this.http.get<LineaOrdenCompra[]>(url);
  }

  createLineaOrdenCompra(newLineaOrdenCompra: LineaOrdenCompraPost): Observable<LineaOrdenCompra> {
    const url = this.API_URL + `/orden_compra_linea`
    return this.http.post<LineaOrdenCompra>(url, newLineaOrdenCompra);
  }

  editLineaOrdenCompra(newLineaOrdenCompra): Observable<LineaOrdenCompra> {
    const url = this.API_URL + `/orden_compra_linea`
    return this.http.put<LineaOrdenCompra>(url, newLineaOrdenCompra);
  }

  deleteLineaOrdenCompra(id: number): Observable<LineaOrdenCompra> {
    const url = this.API_URL + `/orden_compra_linea/${id}`
    return this.http.delete<LineaOrdenCompra>(url);
  }
  
  agregarOrdenesComprasAOrden(body): Observable<any> {
    const url = this.API_URL + `/orden_compra/ordenes_compras_lineas`
    return this.http.post<any>(url, body);
  }

  getCantidadDeOrdenCompra(requestJson): Observable<any> {
    const url = this.API_URL + `/orden_compra_linea/cantidad`
    return this.http.post<any>(url, requestJson)
  }
}
