import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  inject,
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  UntypedFormControl,
  Validators,
} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { ProductoService } from '../../../services/producto.service';
import { Producto } from 'src/app/interfaces/producto.interface';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';
import { Location } from '@angular/common';
import {
  Mercancia,
  MercanciaStock,
} from 'src/app/interfaces/mercancia.interface';
import { MercanciaService } from 'src/app/services/mercancia.service';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';
import { C } from '@fullcalendar/core/internal-common';
import { AlmacenesService } from 'src/app/services/almacenes.service';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'app-ingresar-stock',
  templateUrl: './ingresar-stock.component.html',
  styleUrls: ['./ingresar-stock.component.scss'],
})
export class IngresarStockComponent implements OnInit {
  @ViewChild('precioI') precioInput: ElementRef;
  @ViewChild('stock') stockInput: ElementRef;
  @ViewChild('mercancia') mercanciaInput: ElementRef;
  @ViewChild('ubicacion') ubicacionInput: ElementRef;

  constructor(private el: ElementRef) {}

  private readonly productoService = inject(ProductoService);
  private readonly almacenesService = inject(AlmacenesService);
  private readonly mercanciaService = inject(MercanciaService);
  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);
  private readonly location = inject(Location);
  private readonly confirmationService = inject(ConfirmationService);
  private readonly router = inject(Router);

  public listProductos: Producto[];
  public filteredProductos: Producto[] = [];

  public mercancias: Mercancia[] = [];
  public ubicaciones: Ubicacion[] = [];
  public filteredUbicaciones: Ubicacion[] = [];
  public filteredMercancias: Mercancia[] = [];

  public idProducto: number;
  public barcode: string;
  public codeInputLength: number = 0;
  public soloNumeros: boolean;
  public codigoValido: boolean = false;
  public codigoEanValido: boolean = false;
  public ubicacionValida: boolean = null;
  public ubicacionFocus: boolean = false;
  public almacenValida: boolean = false;
  public almacenFocus: boolean = false;
  public mercanciaValida: boolean = false;
  public mercanciaFocus: boolean = false;

  public loading = false;

  public newStock: FormGroup = this.Form.group({
    codigoEan: [
      null,
      [
        Validators.required,
        Validators.minLength(13),
        Validators.pattern('^[0-9]*$'),
      ],
    ],
    almacen: [null],
    mercancia: [null],
    precio: [null, [Validators.required, Validators.min(0)]],
    stock: [null, [Validators.required, Validators.min(0)]],
    ubicacion: [null, Validators.required],
  });

  public hadSubmit: Boolean = false;

  ngOnInit(): void {
    this.productoService.getProductos().subscribe({
      next: (resp) => {
        console.log(resp);
        this.listProductos = resp;
        this.filteredProductos = resp;
        this.cargarUbicaciones()
      },
    });
  }

  // form getters ----------------------------------------------------------------------------------

  get codigoEan() {
    return this.newStock.get('codigoEan') as UntypedFormControl;
  }
  get almacen() {
    return this.newStock.get('almacen') as UntypedFormControl;
  }
  get mercancia() {
    return this.newStock.get('mercancia') as UntypedFormControl;
  }
  get precio() {
    return this.newStock.get('precio') as UntypedFormControl;
  }
  get stock() {
    return this.newStock.get('stock') as UntypedFormControl;
  }
  get ubicacion() {
    return this.newStock.get('ubicacion') as UntypedFormControl;
  }
  get formValue(): MercanciaStock {
    return this.newStock.value;
  }

  // end form getter --------------------------------------------------------------------------------

  back() {
    this.router.navigateByUrl('/almacen')
  }

  validar(campo: string): boolean {
    if (campo === 'stock' || campo === 'precio') {
      if (this.newStock.get(campo)?.value > 0) {
        return this.newStock.get(campo)?.invalid;
      } else {
        return true;
      }
    }

    return this.newStock.get(campo)?.invalid;
  }

  cargarUbicaciones() {
    this.ubicacionesService.getUbicaciones().subscribe({
      next: (res) => {
        console.log(res);
        this.ubicaciones = res;
        // this.focusUbicacion();
      },
      error: (error) => {
        console.error(error);
        this.messageService.error('Hubo un error al cargar las ubicaciones');
      },
    });
  }

  cargarMercancias(idProducto) {
    this.mercanciaService.getMercanciasByProducto(idProducto).subscribe({
      next: (res) => {
        console.log(res);
        this.mercancias = res;
        this.focusMercancia();
      },
      error: (error) => {
        console.error(error);
        this.messageService.error(
          'Hubo un error al cargar las mercancias de ese producto',
        );
      },
    });
  }

  crearNuevoProducto() {
    // if (this.codigoEanValido && !this.codigoValido) {
    if (!this.codigoValido && this.codeInputLength > 0) {
      this.confirmationService.confirm({
        key: 'crearNuevoProducto',
        accept: () => {
          this.confirmationService.close();
          sessionStorage.setItem('lastUrl', '/almacen/ingresar-stock')
          this.router.navigateByUrl('/almacen/nuevo-producto/' + this.codigoEan.value)
        },
        reject: () => {
          this.confirmationService.close();
        },
      });
    }
  }

  submit(): void {
    this.hadSubmit = true;
    this.loading = true;
    if (this.newStock.invalid) {
      this.loading = false;
      this.messageService.error('Revise los campos');
      return;
    }

    console.log(this.formValue);

    this.mercanciaService.actualizarStock(this.formValue).subscribe({
      next: (response) => {
        this.messageService.add({
          severity: 'success',
          summary: 'Exito!',
          detail: 'Stock Actualizado con exito!',
        });
        console.log(response);
        this.hadSubmit = false;
        this.loading = false;
        this.newStock.reset();
      },
      error: (error) => {
        console.error(error);

        this.loading = false;
        this.messageService.error(error.error.detail)
        return;
      },
    });
    console.log('enviado');
  }

  focusStock() {
    setTimeout(() => {
      document.getElementById('stock').focus();
      //@ts-ignore
      document.getElementById('stock').value = null;
    }, 100);
  }

  focusUbicacion() {
    setTimeout(() => {
      document.getElementById('ubicacion').focus();
      //@ts-ignore
      document.getElementById('ubicacion').value = null;
    }, 100);
  }

  focusAlmacen() {
    setTimeout(() => {
      document.getElementById('almacen').focus();
    }, 100);
  }

  focusMercancia() {
    setTimeout(() => {
      document.getElementById('mercancia').focus();
      //@ts-ignore
      document.getElementById('mercancia').value = null;
    }, 100);
  }

  validarUbicacion(event) {
    setTimeout(() => {
      this.ubicacion.setValue(event.target.value);

      let ubicacionSeleccionada = this.ubicaciones.find(
        (ubi) => ubi.ubicacionId == this.ubicacion.value,
      );

      if (ubicacionSeleccionada !== undefined) {
        this.ubicacionValida = true;
        // setTimeout(() => {
        //   console.log(this.stockInput.nativeElement);
        //   this.stockInput.nativeElement.focus();
        // }, 100);
      } else {
        this.ubicacionValida = false;
        ubicacionSeleccionada = null;
      }
    }, 100);
  }


  validarMercancia(newMercancia: string) {
    setTimeout(() => {
      this.mercancia.setValue(newMercancia);

      let mercanciaSeleccionada = this.mercancias.find(
        (mercancia) => mercancia.id == this.mercancia.value,
      );

      if (mercanciaSeleccionada !== undefined) {
        this.mercanciaValida = true;
        setTimeout(() => {
          console.log(this.precioInput.nativeElement);
          this.precioInput.nativeElement.focus();
        }, 100);
      } else {
        this.mercanciaValida = false;
        mercanciaSeleccionada = null;
      }
    }, 100);
  }

  validarCodigo(event) {
    this.barcode = event.target.value;

    this.codeInputLength = this.barcode.length;

    const regex = /^[0-9]*$/;
    this.soloNumeros = regex.test(this.barcode);

    let productoSeleccionado = this.listProductos.find(
      (producto) => producto.codigoEan == this.barcode,
    );

    if (this.codeInputLength === 13 && this.soloNumeros) {
      this.codigoEanValido = true;
    } else {
      this.codigoEanValido = false;
    }
    if (productoSeleccionado !== undefined) {
      this.codigoValido = true;
      this.idProducto = productoSeleccionado.id;
      // setTimeout(() => {
      //   console.log(this.precioInput.nativeElement);
      //   this.precioInput.nativeElement.focus();
      // }, 100);
    } else {
      this.codigoValido = false;
      productoSeleccionado = null;
    }
  }
}
